<template>
    <div class="PayOver">
      <HomePageTop :TopTitle="TopTotle"  class="bg_fff"></HomePageTop>
      <div v-if="Over==true">
        <div style="" class="SucceedTop">
          <img src="../../../assets/img/common/succeed.png" alt="">
          <p >支付成功</p>
        </div>
        <div data-v-3c369db8="" class="fiexoverTitle mt24"><div data-v-3c369db8="" class="fiexover  border-bD8D8D8"><p data-v-3c369db8="">总价</p> <div data-v-3c369db8="" class="f34 cl_333 h_100 lh102">
          ￥123.56
        </div></div></div>
        <div data-v-3c369db8="" class="fiexoverTitle"><div data-v-3c369db8="" class="fiexover "><p data-v-3c369db8="">实际付款</p> <div data-v-3c369db8="" class="f34 cl_333 h_100 lh102">
          ￥123.56
        </div></div></div>
        <div class="public-btn mt60 mr30 ml30" >查看列表订单</div>
        <router-link :to="{path:'/ShoppingHome'}"  style="" class="getBack">返回</router-link>
      </div>
      <div class="" v-else>
        <div style="" class="SucceedTop">
          <img src="../../../assets/img/common/succeed.png" alt="">
          <p >支付失败</p>
        </div>

        <router-link :to="{path:'/BagOrder'}"  style="" class="public-btn mt60 mr30 ml30 dib" >重新支付</router-link>
        <router-link :to="{path:'/YesOrder'}"  style="" class="getBack">取消</router-link>
      </div>

    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "PayOver",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: { name: "支付状态",left: true},
              Over:true,
            }
        },
        methods: {},
        created() {
          console.log();
          let id=this.$route.params.id;
          if(id==1){
            this.Over=true;
          }else{
            this.Over=false;
          }

        }
    }

</script>

<style scoped>
 .PayOver .SucceedTop{width: 100%;height: 4.72rem;text-align: center;background: #fff;padding-top: 1.4rem;}
 .PayOver .SucceedTop img{width: 1.28rem; margin: .32rem;}
 .PayOver .SucceedTop p{text-align: center;font-size:.34rem;color: #333}
 .PayOver .getBack{width:6.9rem;height: .96rem;background:#fff;border: 1px solid #f90;text-align: center;color: #f90;line-height: .96rem;font-size: .34rem;margin: .32rem auto;display: block}
</style>



